* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Helvetica Neue', sans-serif;
  background-color: rgb(155, 190, 223);
}

.header {
  display: flex;
  justify-content: center;
  padding: 1em;
  color: #FFF;
  background-color: rgb(12, 12, 90);
  border-bottom: 2px solid rgb(6, 3, 32);
  position: relative;
}

.cart-image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  margin: 0;
}

.open-cart-btn {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  cursor: pointer;
  padding: 0;
  border: 2px solid rgb(6, 3, 32);
  position: absolute;
  right: 1em;
}

.close-cart-btn {
  cursor: pointer;
}

.products-heading {
  margin: 1em;
}

.product-dropdown {
  margin: 1em 2em;
}

.products-section {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1em;
}

.product-container {
  width: 23%;
  border: 2px solid grey;
  border-radius: 10px;
}

.footer {
  display: flex;
  justify-content: center;
}